<template>
  <top-nav> 用户注册 </top-nav>
  <div class="register">
    <van-image
      class="register-img"
      width="200"
      height="150"
      src="https://cdn.jsdelivr.net/npm/@vant/assets/cat.jpeg"
    />
    <van-form @submit="onSubmit">
      <van-cell-group inset>
        <van-field
          v-model="name"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="email"
          name="电子邮箱"
          label="电子邮箱"
          placeholder="电子邮箱"
          :rules="[{ required: true, message: '请填写电子邮箱' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-field
          v-model="password_confirmation"
          type="password"
          name="确认密码"
          label="确认密码"
          placeholder="确认密码"
          :rules="[{ required: true, message: '请填写确认密码' }]"
        />
      </van-cell-group>
      <div class="login" @click="goLogin">已有账号！前往登录</div>
      <div style="margin: 16px">
        <van-button
          round
          block
          type="primary"
          native-type="submit"
          class="register-button"
        >
          注册
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import TopNav from "../../components/TopNav.vue";
import { ref, reactive, toRefs, onMounted } from "vue";
import { useRouter } from "vue-router";
import { Notify, Toast } from "vant";
import { regsiter } from "../../network/regsiter";
export default {
  name: "Register",
  components: { TopNav },
  setup() {
    const router = useRouter();
    const userInfo = reactive({
      name: "",
      email: "",
      password: "",
      password_confirmation: "",
    });

    //注册
    const onSubmit = () => {
      if (userInfo.password !== userInfo.password_confirmation) {
        Notify("两次密码不一致");
        return;
      } else {
        regsiter(userInfo).then((res) => {
          if (res.status == "201") {
            Toast.success({ message: "注册成功", forbidClick: true });

            setTimeout(() => {
              router.push({ path: "/login" });
            }, 1000);

            userInfo.password = "";
            userInfo.password_confirmation = "";
          }
        });
      }
    };

    const goLogin = () => {
      router.push({ path: "/login" });
    };

    return {
      ...toRefs(userInfo),
      onSubmit,
      goLogin,
    };
  },
};
</script>

<style lang="scss" scoped>
.register {
  margin-top: 100px;
  .register-img {
    margin-bottom: 20px;
  }
  .login {
    text-align: left;
    margin-top: 10px;
    margin-left: 15px;
    color: #034e94;
  }
  .register-button {
    background-color: #034e94;
    border: none;
    border-radius: 10px;
  }
}
</style>
